.popup{
    display: flex;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity .5s, linear .5s;
}