.popup__save-button{
    height: 50px;
    width: 100%;
    outline: none;
    padding: 0;
    margin-top: 0;
    margin-bottom: 36px;
    background-color: #000000;
    transition: all 0.6s ease-in-out;
    color: #ffffff;
    border-radius: 2px;
    border-width: 0;
    cursor: pointer;
    z-index: 2;
}

.popup__save-button:hover{
    opacity: 0.8;
}

@media (max-width: 500px) {
    .popup__save-button{ 
        font-size: 18px;
        margin-bottom: 20px;
    }
}